
import { AjaxHttp } from '../../../js/ajaxHttp'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import { mapState } from 'vuex'
import event from '../../../js/Event.js'
import { arrContains, isArrayType, getYMFormateTime, getYMDFormateTime, getTimestamp, getHMFormateTime } from "../../../js/utility.js"


export default {
name: 'schedule',
components: {
InlineCalendar,
Group,
Cell,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
computed: {
...mapState([
'scheduleMain'
]),
},
data() {
return {
show: true,
todayValue: '',
range: true,
showLastMonth: false,
showNextMonth: false,
highlightWeekend: false,
return6Rows: true,
hideHeader: false,
hideWeekList: false,
replaceTextList: { 'TODAY': '' },
replace: false,
changeWeeksList: true,
weeksList: ['', '', '', '', '', '', ' '],
useCustomFn: true,
buildSlotFn: () => '<div style="height:19px;"></div>',
disablePast: false,
disableFuture: false,
scheuleArray: [], //
eventArray: [], //
currentTime: '',
}
},
beforeDestroy() {
event.$off("gotoAddData");
},
beforeRouteLeave(to, from, next) {
if (to.name === 'office') {
this.$store.commit('updateScheduleMain', {});
} else {
this.$store.commit('updateScheduleMain', {
type: 'schedule',
scheuleArray: this.scheuleArray,
eventArray: this.eventArray,
currentTime: this.currentTime,
todayValue: this.todayValue,
refresh: false,
});
}
next();
},
mounted() {
event.$on('gotoAddData', function() {
this.$router.push({
name: 'createschedule',
params: {
action: 'default'
}
});
}.bind(this));
this.$store.commit('updateXheaderShowAdd', true);







if (this.scheduleMain.type === 'schedule') {
this.scheuleArray = this.scheduleMain.scheuleArray;
this.currentTime = this.scheduleMain.currentTime;
this.todayValue = this.scheduleMain.todayValue;
if (this.scheduleMain.refresh) {
this.getMonthDeail(this.currentTime);
this.getDayDeail(this.todayValue);
} else {
this.eventArray = this.scheduleMain.eventArray;
this.markDay(this.scheuleArray);
}
} else {
this.buildSlotFn = () => '';
this.currentTime = getYMFormateTime(getTimestamp(new Date()));
this.getMonthDeail(this.currentTime);
this.todayValue = getYMDFormateTime(getTimestamp(new Date()));
this.getDayDeail(this.todayValue);
}
},
methods: {
onChange(val) {

if (this.scheduleMain.todayValue !== val) {
this.scheduleMain.todayValue === val;
if (arrContains(this.scheuleArray, val)) {
this.getDayDeail(val);
} else {
this.eventArray = [];
}
}

},
onViewChange(val, count) {

if (count !== 0) {
var selectTime = val.year + '-' + val.month;
this.getMonthDeail(selectTime);
if (this.currentTime !== '' && selectTime !== this.currentTime) {
this.eventArray = [];
} else if (selectTime === this.currentTime) {
this.getDayDeail(getYMDFormateTime(getTimestamp(new Date())));
}
}
},
onSwipeoutClick(item, index) {
AjaxHttp('POST', 'm=event&a=delete', { id: item.event_id }, this, function(res, wself, success) {
if (success) {
if (res.status === 1) {
wself.$vux.toast.text(res.info, 'top');
wself.eventArray.splice(index, 1);
}
}
});
},
getMonthDeail(date) {
AjaxHttp('POST', 'm=event&a=index', {
date: date
}, this, function(res, wself, success) {
if (success && isArrayType(res.list)) {
wself.scheuleArray = res.list;
wself.markDay(res.list);
}
});
},
markDay(array) {
this.buildSlotFn = (line, index, data) => {
return arrContains(array, data.formatedDate) ? '<div style="font-size:12px;text-align:center;"><span style="display:inline-block;width:5px;height:5px;background-color:#2385CE;border-radius:50%;"></span></div>' : '<div style="height:19px;"></div>'
};
},
getDayDeail(date) {
AjaxHttp('POST', 'm=event&a=view', {
date: date
}, this, function(res, wself, success) {
if (success && isArrayType(res.list)) {
wself.eventArray = res.list;
}
});
},
getHMContent(time) {
return getHMFormateTime(time);
},
enterDetail(item) {
item.type = 'scheduleview'
this.$store.commit('updateDetailView', item);
this.$router.push({
name: 'conmmondetail',
params: {
type: 'scheduleview'
}
});
}
}
}




